html {
  padding: 0;
}

body {
  background-color: #000;
  margin: 0;
  padding: 0;
}

#viewer {
  width: 100%;
  height: 100%;
}

.bottom-bar {
  width: 100%;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
}

.top-bar {
  width: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.left-bar {
  height: 60%;
  display: flex;
  position: fixed;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.right-bar {
  height: 60%;
  display: flex;
  position: fixed;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.left-bar .middle, .right-bar .middle {
  flex-direction: column;
}

.beginning, .middle, .end {
  flex: 1;
  display: flex;
}

.beginning {
  justify-content: flex-start;
}

.middle {
  justify-content: center;
}

.end {
  justify-content: flex-end;
}

